table.digital-grid {
  overflow-x: hidden;
  overflow-y: hidden;
  /* paging */
  /* formatting classes */
}

table.digital-grid > thead > tr > th {
  vertical-align: middle;
  text-align: left;
  /* sorting */
}

table.digital-grid > thead > tr > th table.grid-sort-link {
  cursor: pointer;
}

table.digital-grid > thead > tr > th table.grid-sort-link tbody tr td {
  padding: 0 !important;
  margin: 0 !important;
  border: 0 !important;
  padding-right: 5px !important;
}

table.digital-grid > thead > tr > th table.grid-sort-link .inactive {
  opacity: 0.5;
}

table.digital-grid > tbody > tr td {
  vertical-align: middle;
}

table.digital-grid > tbody > tr:hover {
  background-color: #FAFAFA;
}

table.digital-grid .pagination {
  width: 100%;
  overflow: hidden;
  list-style: none;
  margin: 0;
  padding: 0;
}

table.digital-grid .pagination li {
  float: left;
}

table.digital-grid .pagination li.right {
  text-align: right;
  float: right;
}

table.digital-grid .pagination li.right .page-link {
  border: 0;
}

table.digital-grid > thead > tr > th.bold,
table.digital-grid > tbody > tr td.bold {
  font-weight: bold;
}

table.digital-grid > thead > tr > th.italic,
table.digital-grid > tbody > tr td.italic {
  font-style: italic;
}

table.digital-grid > thead > tr > th.hidden,
table.digital-grid > tbody > tr td.hidden {
  display: none;
}

table.digital-grid > thead > tr > th.center,
table.digital-grid > tbody > tr td.center {
  text-align: center;
}

table.digital-grid > thead > tr > th.left,
table.digital-grid > tbody > tr td.left {
  text-align: left;
}

table.digital-grid > thead > tr > th.right,
table.digital-grid > tbody > tr td.right {
  text-align: right;
}

table.digital-grid > thead > tr > th.nowrap,
table.digital-grid > tbody > tr td.nowrap {
  white-space: nowrap;
}

